import React from 'react';
class Comment extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false,
        };
    }
    render() {
        return (
            <div id="comment" >
                <div style={{ float: 'left', maxWidth: '90%' }}>
                    <p id="text">{this.props.content}</p>
                </div>
                <div style={{ float: 'right', }}>
                    <button id="like"
                        onClick={this.props.record}>
                        like!  {this.props.myLikes}
                    </button><br/>
                    <p>{this.props.myDate}</p>
                </div>
            </div>
        );
    }
}
export default Comment;