import React from 'react';
import $ from 'jquery';

class Intro extends React.Component {
    constructor(){
        super()
        this.state = {
            opacity: Array(6).fill(0)
        }
    }

    componentDidMount() {
        for(let i = 1; i < this.props.myTextArr.length + 1; i++)
          { var k = i.toString();
           document.getElementById("text" + k).style.opacity = this.state.opacity[i-1];
          }
    }

    appear(e) {
        let n = e.target.getAttribute('data-val');  
        let m = parseInt(n) + 1;
        m = m.toString();
        var m_timer = setInterval(()=>{
        let op = this.state.opacity;
        op[n] < 0.7? op[n] += 0.01: clearInterval(m_timer);
        this.setState ({
            opacity: op,
        })
        document.getElementById("text" + m).style.opacity = this.state.opacity[n];
        }, 10);
    }
    /*disappear is not in use*/
    disappear(e) {
    }

    render() {
        var output = [];
        for (let i = 0; i < this.props.myTextArr.length; i++) {
            var ind = i + 1;
            output.push(<input type='text' id={"text" + ind} key={i}
                onMouseOver={this.appear.bind(this)}
                data-val={i}
                value = {this.props.myTextArr[i]}
                 readOnly/>
                );
        }
        return (<div id="magicText">
            {output}
        </div>);
    }
}

export default Intro;