import React from 'react';
import Slide from './Slide.js'
class Activity extends React.Component {
    render() {
        return (<div id = 'activity'>
            <div id='activity_title' style={{ float: 'left' }}>In Forza Horizon<br />
            You can ...</div>
            <Slide myData={this.props.myData} myIndex={this.props.myIndex} dataset={1} next={this.props.next} prev={this.props.prev} myInterval={this.props.myInterval}/>
        </div>);
    }
}
export default Activity;