import React from 'react';
class Slide extends React.Component {
    render() {
        var myPics = [];
        var control = [];
        var m_data = this.props.myData;
        for (let i = 0; i < m_data.name.length; i++) {
            myPics.push(<img key={i}
            src={m_data.picture[this.props.dataset][i]} alt="N/A" 
            style={{ display: i == this.props.myIndex ? 'block' : 'none'}} />);
        }

        if (this.props.dataset == 1) { 
            control.push(<div key={1} id = "RBTN" onClick = {this.props.next}>R</div>);
            control.push(<div key={2} id = "LBTN" onClick = {this.props.prev}>L</div>);
        };

        return (<div className='pictureSlide'>
        {control}{myPics}
        </div>);
    }
}
export default Slide