import React from 'react';
class Buttons extends React.Component {
    render() {
        var myButtons = [];
        var m_data = this.props.myData;
        for (let i = 0; i < m_data.name.length; i++) {
            myButtons.push(<input key={i}
                className={i == this.props.myIndex ? 'active' : 'nonactive'}
                type='button'
                value={''}
                onMouseOver={this.props.onClick}
                data-val={i} />);
        }
        return (
            <div style={{ textAlign: 'center', backgroundColor: 'black' }}> {myButtons}
            </div>
        )
    }

}
export default Buttons;