
import React from 'react';
import background1 from '../Resources/Night.jpg';
import background2 from '../Resources/Forza.jpg';

class Title extends React.Component {
    constructor() {
        super();
        this.state = {
            index: 0,
            opacity: 0.9,
            text: ["Forza Horizon 4", "Introduction"]
        };
        this.timer = Array(2).fill(null);   
    }
    componentDidMount() {
        this.start();
        document.getElementById ("cover").style.opacity = this.state.opacity;

    }

    /* optional function */
    changeColor() {
        var n = this.state.index;
        if (n < this.props.colorArr.length - 1)
            n++;
        else
            n = 0;
        this.setState({
            index: n,
        });

    }

    clear() {
        for(let i = 0; i < this.timer.length; i++)
           clearInterval(this.timer[i]);
    }

    start() {
        var decrease = true;
        var textChange = true;
        var Otext = this.state.text;
        this.timer[0] = setInterval(()=>{
            if (textChange)
            { this.setState(
               { text: ["A Journey", "To Another World"], }
            );
                document.getElementById("title").style.backgroundImage = 'url(' + background1 +')';
                document.getElementById("title_text").style.color = '#f8f';
            }
            else{
                this.setState ({
                    text: Otext,
                });
                document.getElementById("title").style.background = 'url(' + background1 + ')';
                document.getElementById("title_text").style.color = "white";
            }
            textChange = !textChange;   
        }, 10000
        );
        this.timer[1] = setInterval(()=>{
          var op = this.state.opacity; 
          if (decrease)
            { op -= 0.01;
                if (op <= 0)
                decrease = false;
            }
          else
           {
            op += 0.01
              if (op >= 1)
               decrease = true;
           }
          this.setState ({
              opacity: op,
          })
          document.getElementById ("cover").style.opacity = this.state.opacity;
          document.getElementById ("title_text").style.opacity = 1 - this.state.opacity;
        }, 50);
    }

    render() {
        return (<div id='title'><h1 id='title_text' style={{
            textAlign: 'center', backgroundColor: 'black', color: this.props.colorArr[this.state.index] }}>
           {this.state.text[0]}<br/>{this.state.text[1]}
            </h1>
            <div id = "cover"></div>
            </div>);
    }
}

export default Title;