import React from "react";
import player from "../Resources/player.jpg"

class Gamemode extends React.Component {
    constructor(){
       super();
       this.state = {
           carStatus: {speed: 0, power: 50, currentPower: 0, weight: 500, gear: 0, dAccele: 0.5, position: 35},
           gameStatus: {isStart: false, isOver: false, distance: 0, time: 0},
       }
       this.timer = Array(2).fill(null);
}

componentDidMount () {
    var pos = -50;
    setInterval(()=>{
          (pos > 100)? pos = -50: pos+= this.state.carStatus.speed ;
          (pos < -50)? pos = 100: pos+= this.state.carStatus.speed ;
         document.getElementById("bar").style.top = pos +'%';
    }, 100);
}

monitor (e) {
    var m_status = this.state.carStatus;
    var g_status = this.state.gameStatus;
    if (g_status.isOver == false )
    {
        switch(e.key){
        case 'w':
            console.log("w", this.timer)
            var accel = m_status.gear == 'R'? 0.001 : m_status.power*(1-0.06*m_status.gear) * 10 / m_status.weight / (Math.sqrt(m_status.speed) + m_status.weight / 100);
            if (m_status.speed >= 0)
              { m_status.speed += accel;
                if (m_status.speed >= 125)
                    m_status.speed = 125;
              }
            else
               m_status.speed += 0.2;
            break;
        case 's':
            if (m_status.speed >= 0)
                m_status.speed -= m_status.dAccele;
            else 
                m_status.speed += 0.5/(m_status.speed-1);
            break;
        case 'd':
            if (m_status.position < 65) 
            m_status.position += Math.abs(m_status.speed / 50);
            else
            { g_status.isOver = true; g_status.isStart = false; }
            document.getElementById("car").style.left = m_status.position +'%';
               break;
        case 'a':
            if (m_status.position > 29) 
             m_status.position -= Math.abs(m_status.speed / 50);
            else
            { g_status.isOver = true; g_status.isStart = false; }
            document.getElementById("car").style.left = m_status.position +'%';
            break;
           default: 
       }
       this.setState ({
        carStatus: m_status,
        gameStatus: g_status,
    });
    }
    else 
        document.getElementById("over").style.display = "block";
}

handleChange()
{
   document.onkeypress = this.monitor.bind(this)
}

changeGear () {
            var m_status = this.state.carStatus;
            var speed = m_status.speed;
            if (speed >= 0)
            m_status.gear = parseInt(speed*3.6 / 50 + 1);
            else 
            m_status.gear = 'R';
            this.setState ({
                carStatus: m_status,
            });
}


start() {
    this.setState(prevState=>({
        gameStatus:{
            ...prevState.gameStatus,
            isOver: false,
        },
        carStatus:{
            ...prevState.carStatus,
            position: 35,
        }
    }));
    document.getElementById("car").style.left = '35%';
    document.getElementById("over").style.display = "none";
    document.removeEventListener("keypress", this.monitor);
    if (this.state.gameStatus.isStart == false)
    { 
    this.handleChange();
    clearInterval(this.timer);
    var Ospeed = this.state.carStatus.speed;
    this.timer = setInterval(()=>{
        var m_status = this.state.carStatus;
        var g_status = this.state.gameStatus;
        var resist = ( m_status.speed * m_status.speed/80000 + m_status.speed / 400 + m_status.speed * Math.sqrt(Math.sqrt(m_status.weight)) / 20000);  
        var accel = (m_status.speed - Ospeed) / 0.1;
        Ospeed = m_status.speed;
        document.getElementById ("cAccel").value = accel;
        var power = (accel > 0)? ((accel + resist * (m_status.gear == 'R' ? 1: m_status.gear)) * (m_status.weight * Math.abs(m_status.speed) / 746)) : (resist * m_status.weight * m_status.speed / 746);
        document.getElementById("cPower").value = power;
        this.changeGear();
         m_status.speed -= resist;
         g_status.distance += m_status.speed * 0.1;
         g_status.time += 0.1;
        this.setState ({
         carStatus: m_status,
         gameStatus: g_status,
     })
    }, 100)
    this.setState ((prevState)=>({
       gameStatus:{
           ...prevState.gameStatus,
           isStart: true}
    }));
   }
}

record() {
   var power= document.getElementById("pwr").value 
   var weight= document.getElementById("wgt").value;
   var m_status = this.state.carStatus;
   m_status.power = power;
   m_status.weight = weight;
   this.setState({
       carStatus: m_status,
   })
}

render() {
         return (
             <div id = "game">
                 <h1 style={{textAlign: "center", color:"white", fontSize: "4vw"}}>Speed Stimulator</h1>
                 <div id = "gameZone">
                     <div id="speedPad">
                         <h2 style={{textAlign: "center", color: "red"}}>Speedpad</h2>
                         <p><label className="label">Speed (kph):</label>{(this.state.carStatus.speed*3.6).toFixed(2)}</p> 
                         <p><label className="label">Gear:</label>{this.state.carStatus.gear}</p>
                         <p><label className="label">Distance (m):</label>{this.state.gameStatus.distance.toFixed(2)}</p>
                         <p><label className="label">Time (s):</label>{this.state.gameStatus.time.toFixed(2)}</p>
                      </div>
                      <div id="param_bar">
                        <h2 style={{textAlign: "center", color: "red"}}>Parameters</h2>
                        <label className="label">Weight of Car (kg)</label>
                        <input onChange={this.record.bind(this)} type="range" id ="wgt" min='500' max='3000' ></input>
                        <p>{this.state.carStatus.weight}</p>
                        <label className="label">Power of Car (hp)</label>
                        <input onChange={this.record.bind(this)} type="range" id ="pwr" min='50' max='1500' ></input>
                        <p>{this.state.carStatus.power}</p>
                        <button id = "gameStart" onClick = {this.start.bind(this)}>Start</button>
                      </div>
                      <div id = "lRoad" className="road"></div>
                      <div id = "rRoad" className="road"></div>
                      <div id = "bar" className="road"></div>
                      <div id = "car" className="road"><img src={player}></img></div>
                      <div id = "over">GAME OVER</div>
                 </div>
                 <div id = "stat">
                   <div style={{paddingBottom: "1%"}}>
                       <label style={{color: "white"}}>Current power (hp)</label>
                       <meter id="cPower" className="current" low="500" high="1000" min="0" max="1500" optimum='1250'></meter>
                    </div>
                    <div style={{paddingBottom: "1%"}}> 
                        <label style={{color: "white"}}>Current acceleration (m/s<sup>2</sup>)</label>
                        <meter id="cAccel" className = "current" low="5" high="10" min="0" max="40" optimum='12.5'></meter>
                    </div>
                 </div>
                 <div id = "inst">
                     <h1>Instruction</h1>
                     <p> Adjust range bars to change the power and weight of your car.</p> 
                     <p> Click 'start' to start or restart the game. </p>
                     <p> Press 'w','s','a','d' to move up, down, left, and right.</p>
                 </div>
             </div>
         );
    }
}

export default Gamemode;