import React from 'react';


function Data (props) {
    var stat = props.stat;
    return (
        <div id="data_into">
            <p>About <span className='stat_num'>{stat[0]}</span> kilometers of road to explore</p>
            <img id={0} onMouseEnter={props.refresh} src={props.myPic[0]} alt="loading error"/>
            <p>More than <span className='stat_num'>{stat[1]}</span> cars to own</p>
            <img id={1} onMouseEnter={props.refresh} src={props.myPic[1]} alt="loading error"/>
            <p>Around <span className='stat_num'>{stat[2]}</span> square kilometers of open world</p>
            <img id={2} onMouseEnter={props.refresh} src={props.myPic[2]} alt="loading error"/>
            <p>A community of more than <span className='stat_num'>{stat[3]}</span> players</p>
            <img id={3} onMouseEnter={props.refresh} src={props.myPic[3]} alt="loading error"/>
        </div>
    )
}

export default Data;