import React from 'react';
import $ from 'jquery';
import ReactDOM from 'react-dom';
import './sstyle.css';
import pic1 from './Resources/a.jpg'
import pic2 from './Resources/b.jpg'
import pic3 from './Resources/c.jpg'
import pic4 from './Resources/d.jpg'
import pic5 from './Resources/e.jpg'
import pic6 from './Resources/f.jpg'
import pic7 from './Resources/1.jpg'
import pic8 from './Resources/2.jpg'
import pic9 from './Resources/3.jpg'
import pic10 from './Resources/4.jpg'
import pic11 from './Resources/5.jpg'
import pic12 from './Resources/6.jpg'
import pic13 from './Resources/11.jpg'
import pic14 from './Resources/22.jpg'
import pic15 from './Resources/33.jpg'
import pic16 from './Resources/44.jpg'
import add1 from './Database/comment_data.txt'
import Menu from './Components/Menu.js'
import Title from './Components/Title.js'
import Slide from './Components/Slide.js'
import Buttons from './Components/Button.js'
import Intro from './Components/Intro.js'
import Activity from './Components/Activity.js'
import Data from './Components/Data.js'
import Feedback from './Components/Feedback.js'
import Comment from './Components/Comment.js'
import Video from './Components/Video.js'
import Gamemode from './Components/Gamemode.js'
import Form from './Components/Form.js'

//const port//
const HOSTADD = "http://localhost:3000/"

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            currentIndex: 0,
            m_length: 0,
            num_stat: Array(4).fill('???'),
            isRendered: Array(4).fill(false),
            comment: [],
            numLikes: Array(this.props.maxNumOfComments).fill(0),
            commentDates: [],
            isFull: false,
        };
        this.m_timer= Array(4).fill(null);
    }

    //============lifecycle============//
    componentWillMount() {
        var xhttp = new XMLHttpRequest();
        var m_this = this;
        xhttp.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200)
            {
                var data_Comment = JSON.parse(xhttp.responseText).comment;
                var data_Likes = JSON.parse(xhttp.responseText).numLikes;
                var data_Dates = JSON.parse(xhttp.responseText).commentDates;
            m_this.setState({
                comment: data_Comment,
                numLikes: data_Likes,
                commentDates: data_Dates,
            });
        }
        }
        xhttp.open("GET", add1, true);
        xhttp.onload = function () {
            console.log(xhttp.responseText)
        }.bind(this)
        xhttp.send();

        this.setState(
            {
                m_length: this.props.myData.name.length,
            });
    }

    componentDidMount() {
        this.AutoPlay();
    }
    //==============Form functions======================//
    popForm () {
        var logInPage = document.getElementById("logIn");
        var w = window.innerWidth;
        var h = window.innerHeight;
        logInPage.style.left = w/2  + 'px';
        logInPage.style.top = h/2  + 'px';
        logInPage.style.display ="block";
        this.m_timer[3] = setInterval(()=>{ 
            w = window.innerWidth;
            h = window.innerHeight;
            logInPage.style.left = w/2  + 'px';
            logInPage.style.top = h/2  + 'px';
            },  200);
    }

    cancel () {
        this.clear(this.m_timer[3]);
        var logInPage = document.getElementById("logIn");
        logInPage.style.display = "none";
    }
    //==============Interactive functions===============//

    changeData (m_this) {
        var currentJSON = JSON.stringify(m_this.state);
        console.log(currentJSON);
        $.ajax(
           { type: 'POST',
            url:HOSTADD+currentJSON,
            data: currentJSON,
            dataType: 'text',
          });
        this.getData();
    }

    getData () {
        var xhttp = new XMLHttpRequest();
        var m_this = this;
        xhttp.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200)
            {
                var data_Comment = JSON.parse(xhttp.responseText).comment;
                var data_Likes = JSON.parse(xhttp.responseText).numLikes;
                var data_Dates = JSON.parse(xhttp.responseText).commentDates;
            m_this.setState({
                comment: data_Comment,
                numLikes: data_Likes,
                commentDates: data_Dates,
            });
        }
        }
        xhttp.open("GET", add1, true);
        xhttp.onload = function () {
            console.log(xhttp.responseText)
        }.bind(this)
        xhttp.send();

        this.setState(
            {
                m_length: this.props.myData.name.length,
            });
    }


    //==============Slide functions==============//

    AutoPlay() {
        this.clear(this.m_timer[0]);
        this.m_timer[0] = setInterval(() => {
            let v = this.state.currentIndex;
            if (v >= this.state.m_length - 1)
                v = 0;
            else
                v++;
            this.setState(
                {
                    currentIndex: v,
                });
        }, this.props.myInterval
        );
    }

    resume() {
        this.AutoPlay();
    }

    change(e) {
        var value = e.target.getAttribute('data-val');
        this.setState(
            {
                currentIndex: value,
            });
    }

    clear(v) {
        clearInterval(v);
    }

    moveNext() {
        var ind = this.state.currentIndex + 1 ;
        ind >= this.props.myData.picture[0].length && (ind = 0);
        this.setState ({
            currentIndex: ind,
        });
        this.resume();
    }

    movePrev() {
        var ind = this.state.currentIndex - 1;
        ind < 0  && (ind = this.props.myData.picture[0].length - 1);
        this.setState ({
            currentIndex: ind,
        });
        this.resume();
    }

    //=============Data functions=============//
    increment(e) {
        var i = e.target.getAttribute('id');
        if (this.state.isRendered[i] == false) {
            var defaultArr = this.state.num_stat;
            defaultArr[i] = 0;
            this.setState({
                num_stat: defaultArr,
            })
            this.m_timer[1] = setInterval(this.update.bind(this,i), this.props.myData.refreshRate);
            if (this.state.num_stat == this.props.myData.stat)
                this.clear(this.m_timer[1]);
        }
        var newRenderState = this.state.isRendered;
        newRenderState[i] = true;
        this.setState({
            isRendered: newRenderState,
        })
    } 


    /*optional & not included*/
    end() {
        this.setState({
            isRendered: Array(4).fill(true),
        });
    }
    

    update(i) {
        var currentArr = this.state.num_stat;
        var finalArr = this.props.myData.stat;
        if (currentArr[i] < finalArr[i]) {
            currentArr[i] += Math.floor((finalArr[i] - currentArr[i]) / (this.props.myData.exponentialRate / (i + 1))) == 0 ?
                1 :
            Math.floor((finalArr[i] - currentArr[i]) / (this.props.myData.exponentialRate / (i + 1)));
        }
        this.setState({
            num_stat: currentArr,
        })
    }

    //=============Comment functions============//

    recordContent (content, date) {
        var num = this.state.comment.length;
        var com = this.state.comment;
        var m_date = this.state.commentDates;
        if (content.substring(0,3)=='CLR')
        {
            console.log(content);
            var commentToClear = content.substring(3);
            $.ajax(
                { type: 'POST',
                  url:HOSTADD+"CLR"+commentToClear,
                  data: commentToClear,
                  dataType: "text",
               });
        }
        else if (content != ''&& num < this.props.maxNumOfComments) {
                com.push(content);
                m_date.push(date);
                this.setState(
                    {
                        comment: com,
                        isFull: false,
                        commentDates: m_date,
                    });
                this.changeData(this);
        }
        else {
                this.setState(
                    {
                        isFull: true,
                    });
        }
        
    }

    shiftUp(ind) {
        while (ind != 0) {
            if (this.state.numLikes[ind] > this.state.numLikes[ind - 1]) {
                var firstComment = this.state.comment[ind];
                var firstLikes = this.state.numLikes[ind];
                var firstDates = this.state.commentDates[ind];

                var secondComment = this.state.comment[ind - 1];
                var secondLikes = this.state.numLikes[ind - 1];
                var secondDates = this.state.commentDates[ind - 1];

                var newComments = this.state.comment;
                var newLikes = this.state.numLikes;
                var newDates = this.state.commentDates;

                newLikes[ind - 1] = firstLikes;
                newLikes[ind] = secondLikes;

                newComments[ind - 1] = firstComment;
                newComments[ind] = secondComment;

                newDates[ind - 1] = firstDates;
                newDates[ind] = secondDates;

                this.setState({
                    comment: newComments,
                    numLikes: newLikes,
                })
            }
            ind--;
        }
    }

    recordLikes(ind) {
        var newNumLikes = this.state.numLikes;
        newNumLikes[ind]++;
        this.setState({
            numLikes: newNumLikes,
        });
        this.shiftUp(ind);
        this.changeData(this);
    }

    render() {
        //render comments//
        var comments = this.state.comment;
        var num = comments.length;
        if (num < this.props.maxNumOfComments) {
            var warning = "";
        }
        else
            warning = "Reach the maximum number of comments";
        var list = comments.map((value, index) => {
            return (
                <Comment key={index} content={value} record={this.recordLikes.bind(this, index)} myLikes={this.state.numLikes[index]} myDate={this.state.commentDates[index]}/>
            )
        })

        return (<div>
            <Menu popForm={this.popForm.bind(this)}/>
            <div id="logIn">
                <div id="cancelBtn" onClick={this.cancel.bind(this)}><p>â•³</p></div>
                <Form/>
            </div>
            <div onMouseOver={this.clear.bind(this, this.m_timer[0])} onMouseOut={this.resume.bind(this)}>
                <Slide myData={this.props.myData} myIndex={this.state.currentIndex} dataset={0}/>
            </div>
            <Buttons myData={this.props.myData} onClick={this.change.bind(this)} myIndex={this.state.currentIndex} myInterval={this.props.myInterval}/>
            <Title colorArr={this.props.myTitleColors}/>
            <Intro myTextArr={this.props.myIntroPhrases}/>
            <Activity myData={this.props.myData} myIndex={this.state.currentIndex} next={this.moveNext.bind(this)} prev={this.movePrev.bind(this)} myInterval={this.props.myInterval}/>
            <div>
                <Data refresh={this.increment.bind(this)} stat={this.state.num_stat} myPic={this.props.myData.picture[2]}/>
            </div>
            <Video maxNumOfVideos = {this.props.maxNumOfVideos}/>
            <Gamemode myData={this.props.myData}/>
            <div id='commentArea'>
                <h1 id = "commentTitle">Comments:</h1>
                {list}
            </div>
            <div id='feedback'>
                <Feedback record={(content, date) => this.recordContent(content, date)} />
                <p style={{ color: 'red' }}> {warning} </p>
            </div>
        </div>)
    }
}

ReactDOM.render(<App myData={{
    picture: [
    [pic1, pic2, pic3, pic4, pic5, pic6],
    [pic7, pic8, pic9, pic10, pic11, pic12], 
    [pic13, pic14, pic15, pic16], 
    ],
    name: ['1', '2', '3', '4', '5', '6'],
    stat: [200, 450, 7146, 10000000], refreshRate: 5, exponentialRate: 500
}}
    myInterval={5000}
    myTitleColors={['white', 'yellow', 'lightgrey', 'lightblue', 'pink', 'orange', 'lightgreen']}
    myIntroPhrases={['Fantastic Experience You Will Never Forget',
        'Greatest Scenery Ever in Gaming',
        'Immersive and Realistic',
        'A Piece of Art',
        'Well Balanced between Simulation and Entertainment',
        'Enjoy and Start Your Journey'
    ]}
    maxNumOfComments={15}
    maxNumOfVideos={3}
/>, document.getElementById('root'));

//myData | all the picture, video and other data =>
/* picture | all the pictures,
   name | the sequential name of data, 
   stat | the stats as an array
   refreshRate | the rate to update the stat
   exponentialRate | the acceleration of the update of the stat
*/
//myInterval | the refresh time of the image
//myTitleColor | the collection of changing colors of title
//myIntroPhrases | the phrases appear in the introduction
//maxNumOfComments | the maximum number of comments allowed
//maxNumOfVideos | the maximum number of videos allowed


    



