import React from 'react';

class Menu extends React.Component {

    constructor(){
        super();
        this.state= {
            change: false,
            toShow: [],
            destination: ["#comment", "#game", "#magicText","#title"]
        }
    }

    componentDidMount() {
        this.timer = setInterval(()=>{
            var w = window.innerWidth;
            var n_toShow = [];
            if (w > 800)
            {
                for(let i= 0; i < 5; i ++)
                document.getElementsByClassName("menulist")[i].style.display = "block";
                n_toShow = [];
            }
            else if (w > 700) {
                for(let i= 0; i < 4; i ++)
                document.getElementsByClassName("menulist")[i].style.display = "block";
                document.getElementsByClassName("menulist")[3].style.display = "none";
                for(let i= 0; i < 1; i ++)
                n_toShow[i] = document.getElementsByClassName("menulist")[3-i].innerHTML; 
            }
            else if (w > 500) {
                for(let i= 2; i < 4; i ++)
                document.getElementsByClassName("menulist")[i].style.display = "none";

                document.getElementsByClassName("menulist")[0].style.display = "block";
                document.getElementsByClassName("menulist")[1].style.display = "block"; 
                for(let i= 0; i < 2; i ++)
                n_toShow[i] = document.getElementsByClassName("menulist")[3-i].innerHTML; 
            } 
            else if (w > 400){
                for(let i= 1; i < 4; i ++)
                document.getElementsByClassName("menulist")[i].style.display = "none";

                document.getElementsByClassName("menulist")[0].style.display = "block";
                for(let i= 0; i < 3; i ++)
                n_toShow[i] = document.getElementsByClassName("menulist")[3-i].innerHTML; 
            }
            else{
                for(let i= 0; i < 4; i ++)
                document.getElementsByClassName("menulist")[i].style.display = "none";
                for(let i= 0; i < 4; i ++)
                n_toShow[i] = document.getElementsByClassName("menulist")[3-i].innerHTML; 
            }
            this.setState({
                toShow: n_toShow,
            })
        }, 50);
    }

    expand() {
        var m_change = this.state.change;
        this.setState({
            change:!m_change,
        })
    }

    render() {
        var address = this.state.destination;
        var text = this.state.toShow;
        var listStack = [];
        for (let i=0; i<text.length; i++)
            listStack.push(<li style={{float: "none", zIndex: 10}}key={i}><a href = {address[i]}>{text[i]}</a></li>)

        var toRender= null;
        (this.state.change)? toRender = listStack : toRender = null;
       return( 
       <div id = "menu">
           <div id= "head">
           </div>
            <div id = "topbar"> 
              <ul id = "options">
               <li><a className= "menulist" href = "#title">Home</a></li>
               <li><a className= "menulist" href = "#magicText">Intro</a></li>
               <li><a className= "menulist" href = "#game">Game</a></li>
               <li><a className= "menulist" href = "#comment">Comments</a></li>
               <li id="expand"><a className= "menulist" onClick={this.expand.bind(this)}>More â–¼</a></li>
             </ul>
             <ul id = "dropBox">
             {toRender}
             </ul>
             <div id = "signIn">
             <button id = "signInBtn" onClick={this.props.popForm}>Signin</button>
              </div> 
            </div>
        </div>);
    }
}



export default Menu;
